export default function Three() {
  return (
    <ThreeWrapper>
      <Canvas
        camera={{
          position: [100, 10, 10],
          far: 10000,
        }}
      >
        <OrbitControls minDistance={40} maxDistance={330} />
        <Outer />
        <PlanetEarth />
        <directionalLight
          intensity={0.3}
          color={'#12c2e9'}
          position={[5, 40, 10]}
          castShadow
        />
        <directionalLight
          intensity={0.7}
          color={'#f64f59'}
          position={[-10, -10, -10]}
          castShadow
        />
      </Canvas>
    </ThreeWrapper>
  );
}
